/**
 * Financial Drag Assessment Embed Loader
 */

import type { CompletionData } from '@/types'

interface AssessmentConfig {
  type: 'financial-drag'
  element: HTMLElement | null
  version?: string
  onComplete?: (data: CompletionData) => void
  onReady?: () => void
  onResize?: (height: number) => void
  options?: {
    sessionId?: string
    baseUrl?: string
  }
}

interface PostMessageEvent {
  type: string
  payload?: unknown
}

type QueueItem = AssessmentConfig | ((...args: unknown[]) => void)

const instances = new Map<HTMLElement, HTMLIFrameElement>()

const DEFAULT_BASE_URL = (function() {
  const scripts = document.getElementsByTagName('script')
  for (let i = scripts.length - 1; i >= 0; i--) {
    const src = scripts[i].src
    if (!src) continue

    try {
      const url = new URL(src)
      const pathname = url.pathname
      if (pathname.endsWith('/embed.min.js') || pathname.endsWith('/embed.js')) {
        return url.origin + pathname.replace(/\/embed(\.min)?\.js$/, '')
      }
    } catch {
      continue
    }
  }
  return ''
})()

function createIframe(config: AssessmentConfig): HTMLIFrameElement | null {
  const { element, type, version, options } = config

  if (!element) {
    console.error('[FinancialDrag] No element provided')
    return null
  }

  const existing = instances.get(element)
  if (existing) {
    existing.remove()
    instances.delete(element)
  }

  const baseUrl = options?.baseUrl || DEFAULT_BASE_URL
  const versionPath = version ? `v${version}` : 'latest'
  let iframeSrc = `${baseUrl}/${versionPath}/index-embed.html`

  const params = new URLSearchParams()
  params.set('type', type)
  params.set('embed', 'true')
  if (options?.sessionId) {
    params.set('session', options.sessionId)
  }
  iframeSrc += `?${params.toString()}`

  const iframe = document.createElement('iframe')
  iframe.src = iframeSrc
  iframe.style.cssText = `
    width: 100%;
    min-height: 600px;
    border: none;
    display: block;
    overflow: hidden;
  `
  iframe.setAttribute('allowfullscreen', 'true')
  iframe.setAttribute('allow', 'clipboard-write')

  instances.set(element, iframe)

  element.innerHTML = ''
  element.appendChild(iframe)

  return iframe
}

function handleMessage(event: MessageEvent, config: AssessmentConfig, iframe: HTMLIFrameElement) {
  const data = event.data as PostMessageEvent

  if (!data || typeof data.type !== 'string') {
    return
  }

  if (!data.type.startsWith('redwood:assessment:')) {
    return
  }

  switch (data.type) {
    case 'redwood:assessment:ready':
      config.onReady?.()
      break

    case 'redwood:assessment:complete':
      config.onComplete?.(data.payload as CompletionData)
      break

    case 'redwood:assessment:resize': {
      const { height } = data.payload as { height: number }
      if (height && height > 0) {
        iframe.style.height = `${height}px`
        config.onResize?.(height)
      }
      break
    }

    case 'redwood:assessment:navigate': {
      break
    }
  }
}

function initAssessment(config: AssessmentConfig): void {
  if (!config.element) {
    console.error('[FinancialDrag] Element not found')
    return
  }

  const iframe = createIframe(config)
  if (!iframe) {
    return
  }

  const messageHandler = (event: MessageEvent) => {
    if (event.source !== iframe.contentWindow) {
      return
    }
    handleMessage(event, config, iframe)
  }

  window.addEventListener('message', messageHandler)

  const observer = new MutationObserver(() => {
    if (!document.contains(iframe)) {
      window.removeEventListener('message', messageHandler)
      observer.disconnect()
      if (config.element && instances.get(config.element) === iframe) {
        instances.delete(config.element)
      }
    }
  })
  observer.observe(document.body, { childList: true, subtree: true })
}

function processQueue(queue: QueueItem[]): void {
  for (const item of queue) {
    if (typeof item === 'function') {
      item()
    } else {
      initAssessment(item)
    }
  }
}

function init(): void {
  const existingQueue = (window as unknown as { FinancialDragAssessment?: QueueItem[] }).FinancialDragAssessment

  const api = {
    push(config: AssessmentConfig | ((...args: unknown[]) => void)): void {
      if (typeof config === 'function') {
        config()
      } else {
        if (document.readyState === 'loading') {
          document.addEventListener('DOMContentLoaded', () => initAssessment(config))
        } else {
          initAssessment(config)
        }
      }
    },

    init(config: AssessmentConfig): void {
      this.push(config)
    },

    destroy(element: HTMLElement): void {
      const iframe = instances.get(element)
      if (iframe) {
        iframe.remove()
        instances.delete(element)
      }
    },

    version: '1.0.0',
  }

  ;(window as unknown as { FinancialDragAssessment: typeof api }).FinancialDragAssessment = api

  if (Array.isArray(existingQueue)) {
    processQueue(existingQueue)
  }
}

if (typeof window !== 'undefined') {
  init()
}

export { init, initAssessment }
export type { AssessmentConfig, CompletionData }
